.game {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.players {
  display: flex;
  justify-content: space-between;
  padding-bottom: 15px;
}

.player {
  padding: 8px;
  padding-left: 6px;
  font-size: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100px;
  height: 50px;
  border-radius: 10px;
  border: 1px solid #000;
}

.player[data-player='black'] {
  background: linear-gradient(90deg, rgba(250, 250, 250, 0.1) 0%, rgba(0, 0, 0, 0.75) 85%), url("https://cabins-seeds.s3.amazonaws.com/newblack.jpg");
  background-size: cover;
}

.player[data-player='white'] {
  flex-direction: row;
  background: linear-gradient(90deg, transparent 15%, rgba(250, 250, 250, 0.5) 100%), url('https://cabins-seeds.s3.amazonaws.com/whitewood.jpg');
  background-size: cover;
}